import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import CheckBox from '../CheckBox/CheckBox';
import styles from './SelectMenuStyles';
import ColumnsSymbol from './static/ColumnsSymbol';

/* eslint-disable jsx-a11y/label-has-for */
class SelectMenu extends React.Component {
    constructor(props) {
        super(props);
        this.resetHide = this.resetHide.bind(this);
        this.initHide = this.initHide.bind(this);
        this.handleClickOut = this.handleClickOut.bind(this);
        this.toggleSelectMenu = this.toggleSelectMenu.bind(this);
        this.menuIsActive = false;
        this.state = {
            shouldShowMenu: false,
        };
    }

    componentDidMount() {
        // onBlur event on button in Safari doesnt work
        window.addEventListener('mousedown', this.handleClickOut);
    }

    componentWillUnmount() {
        // onBlur event on button in Safari doesnt work
        window.removeEventListener('mousedown', this.handleClickOut);
    }

    handleClickOut(e) {
        const target = e ? e.target : null;
        if (!this.menuIsActive && this.clickedOutsideButton(target)) {
            this.setState({ shouldShowMenu: false });
        }
    }

    clickedOutsideButton(target) {
        if (target === this.menubutton) {
            return false;
        }
        let node = target && target.parentNode;
        while (node) {
            if (node === this.menubutton) {
                return false;
            }
            node = node.parentNode;
        }
        return true;
    }

    initHide() {
        // firefox needs focus
        this.menubutton.focus();
        this.menuIsActive = false;
    }

    resetHide() {
        this.menuIsActive = true;
    }

    toggleSelectMenu(e) {
        const target = e.target;
        setTimeout(() => { target.focus(); }, 10);
        const { shouldShowMenu } = this.state;
        this.setState({ shouldShowMenu: !shouldShowMenu });
    }

    render() {
        const { shouldShowMenu } = this.state;
        const { columns, onChange, styleName } = this.props;
        const style = styles(styleName);
        const enabled = columns.length > 0;
        return (
            <div style={style.wrapper}>
                <button
                    type="button"
                    data-handle="select-menu-button"
                    tabIndex="0"
                    key={'btn3'}
                    style={enabled ? style.menubutton : style.menubuttonDisabled}
                    disabled={!enabled}
                    onClick={this.toggleSelectMenu}
                    ref={(mb) => { this.menubutton = mb; }}
                >
                    <ColumnsSymbol color={enabled ?
                        defaultStyles.color3 : defaultStyles.color6}
                    />
                </button>
                {shouldShowMenu && (
                    <div style={style.parent}>
                        <div style={style.menuArrowShadow}>&nbsp;</div>
                        <div
                            ref={(r) => { this.menu = r; }}
                            data-handle="select-menu"
                            onMouseEnter={this.resetHide}
                            onMouseLeave={this.initHide}
                            style={style.menu}
                        >
                            <div data-handle={'select-menu-header'} style={style.header}>Column selection:</div>
                            { columns.map(column => (
                                <div style={style.row} key={column.key}>
                                    <CheckBox
                                        data-handle="select-menu-checkbox"
                                        key={column.key}
                                        onSetValue={() =>
                                            onChange(column.key, !column.isVisible)
                                        }
                                        styleName="listStyles"
                                        value={column.isVisible}
                                        name={`sel-${column.key}`}
                                    >
                                        <span style={style.row.span} >
                                            {column.title}
                                        </span>
                                    </CheckBox>
                                </div>
                            ))}
                        </div>
                        <div style={style.menuArrow}>&nbsp;</div>
                    </div>
                )}
            </div>
        );
    }
}

SelectMenu.propTypes = {
    onChange: PropTypes.func.isRequired,
    columns: PropTypes.arrayOf(PropTypes.shape).isRequired,
    styleName: PropTypes.string,
};

export default SelectMenu;
