import PropTypes from 'prop-types';
import React from 'react';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import styles from './ListStyles';
import TabArrow from './static/TabArrow';
import PageArrow from './static/PageArrow';

class Pagination extends React.Component {
    constructor(props) {
        super(props);
        this.onChangePage = this.onChangePage.bind(this);
        this.state = {
        };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillMount() {
        this.updateComponentState(this.props);
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { offset: oldOffset, totalNumberOfResults: oldTotalNumberOfResults } = this.props;
        const { offset, totalNumberOfResults } = nextProps;

        if (offset !== oldOffset || totalNumberOfResults !== oldTotalNumberOfResults) {
            this.updateComponentState(nextProps);
        }
    }

    onChangePage(currentPage) {
        this.setState({ currentPage });
    }

    updateComponentState(props) {
        const { offset, chunkSize, totalNumberOfResults } = props;
        this.setState({
            currentPage: Math.ceil(offset / chunkSize),
            numPages: Math.ceil(totalNumberOfResults / chunkSize),
        });
    }


    render() {
        const { offset, styleName, chunkSize, onGoToPage, totalNumberOfResults } = this.props;
        const { currentPage, numPages } = this.state;

        const style = styles(styleName).list;

        const canNavRight = currentPage < numPages;
        const canNavLeft = offset > 1;
        const firstOffset = 1;
        const lastOffset = ((numPages - 1) * chunkSize) + 1;
        const nextOffset = offset + chunkSize;
        const prevOffset = offset - chunkSize;

        return ((totalNumberOfResults / chunkSize) > 1) && (
            <div style={style.navigationGroup} data-handle="pageNavigation">
                <button
                    type="button"
                    data-handle="goFirst"
                    style={style.arrowButton}
                    disabled={!canNavLeft}
                    onClick={() => onGoToPage(firstOffset)}
                >
                    <TabArrow color={canNavLeft ? defaultStyles.color1 : defaultStyles.color9} />
                </button>
                <button
                    type="button"
                    data-handle="goPrev"
                    style={style.arrowButton}
                    disabled={!canNavLeft}
                    onClick={() => onGoToPage(prevOffset)}
                >
                    <PageArrow color={canNavLeft ? defaultStyles.color1 : defaultStyles.color9} />
                </button>
                <input
                    data-handle={'goTo'}
                    value={currentPage}
                    style={style.goTo}
                    type="number"
                    min="1"
                    max={numPages}
                    onChange={e => this.onChangePage(e.target.value)}
                    onBlur={(e) => {
                        let newOffset = ((+e.target.value - firstOffset) * chunkSize) + firstOffset;
                        if (newOffset > lastOffset) newOffset = lastOffset;
                        if (newOffset < firstOffset) newOffset = firstOffset;
                        onGoToPage(newOffset);
                    }}
                />
                <span style={style.pageCounter}>
                    { `/${numPages}` }
                </span>
                <button
                    type="button"
                    data-handle="goNext"
                    style={style.arrowButton}
                    disabled={!canNavRight}
                    onClick={() => onGoToPage(nextOffset)}
                >
                    <PageArrow
                        direction="right"
                        color={canNavRight ? defaultStyles.color1 : defaultStyles.color9}
                    />
                </button>
                <button
                    type="button"
                    data-handle="goLast"
                    style={style.arrowButton}
                    disabled={!canNavRight}
                    onClick={() => onGoToPage(lastOffset)}
                >
                    <TabArrow
                        direction="right"
                        color={canNavRight ? defaultStyles.color1 : defaultStyles.color9}
                    />
                </button>
            </div>
        );
    }
}

Pagination.propTypes = {
    chunkSize: PropTypes.number,
    offset: PropTypes.number,
    onGoToPage: PropTypes.func,
    styleName: PropTypes.string,
    totalNumberOfResults: PropTypes.number,
};

export default Pagination;
