const easing = (t, b, c, d) => {
    // t = current time or position
    // b = begin value
    // c = change or delta of value
    // d = duration / total time or position
    const time = (t / d) - 1;
    return (c * (((time) * (time * time * time * time)) + 1)) + b;
};

const animate = (scrollContainer, start, end, duration) => {
    const c = scrollContainer;
    const delta = end - start;
    let startTime;

    const getTime = () => {
        if (window.performance && window.performance.now) {
            startTime = window.performance.now();
        } else if (Date.now) {
            startTime = Date.now();
        } else {
            startTime = new Date().getTime();
        }
    };

    const step = (timestamp) => {
        const t = (!timestamp ? 0 : timestamp - startTime);
        const factor = easing(t, 0, 1, duration);
        c.scrollLeft = start + (delta * factor);
        if (t < duration) {
            window.requestAnimationFrame(step);
        } else {
            c.scrollLeft = end;
        }
    };

    getTime();
    step();
};

export default animate;
