import React from 'react';
import PropTypes from 'prop-types';

const ListArrow = ({ color, direction }) => {
    let transformValue;
    switch (direction) {
    case 'up':
        transformValue = 'rotate(90)';
        break;
    case 'right':
        transformValue = 'rotate(180)';
        break;
    case 'down':
        transformValue = 'rotate(270)';
        break;
    default:
        break;
    }

    return (
        <svg
            x="0px"
            y="0px"
            width="100%"
            height="100%"
            viewBox="0 0 24 57.8"
            xmlSpace="preserve"
            transform={transformValue}
        >
            <polygon fill={color} points="19.8,57.8 0,28.9 19.8,0 24,2.9 6.1,28.9 24,54.9 " />
        </svg>
    );
};
ListArrow.propTypes = {
    color: PropTypes.string,
    direction: PropTypes.string,
};

ListArrow.defaultProps = {
    color: 'red',
};

export default ListArrow;
