import React from 'react';
import PropTypes from 'prop-types';
import getStyle from './InputComboBoxStyles';

class InputComboBox extends React.Component {
    constructor(props) {
        super(props);
        this.inputRef = undefined;
        this.onKeyDown = this.onKeyDown.bind(this);
        this.onMouseDown = this.onMouseDown.bind(this);
        this.shouldRenderOptions = this.shouldRenderOptions.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
        this.optionClicked = this.optionClicked.bind(this);
        this.state = {
            littleArrowClicked: false,
            dropDownIndex: -1,
            userTyping: false,
        };
    }

    onChange(e) {
        const {
            onChange,
        } = this.props;
        onChange(e);
        this.setState({
            dropDownIndex: -1,
            userTyping: true,
        });
    }

    onFocus(e) {
        const { onFocus } = this.props;
        onFocus(e);
        this.setState({
            dropDownIndex: -1,
        });
        e.target.select();
    }

    onBlur(e) {
        const {
            value,
            onBlur,
            onSetValue,
        } = this.props;

        onSetValue(value);
        this.setState({
            dropDownIndex: -1,
            userTyping: false,
            littleArrowClicked: false,
        });
        onBlur(e);
    }

    onKeyDown(e) {
        const { onCancel, onSetValue, value, options } = this.props;
        const { dropDownIndex } = this.state;

        e.persist();

        switch (e.key) {
        case 'Enter': {
            const newValue = dropDownIndex > -1 ? options[dropDownIndex].value : value;
            onSetValue(newValue);
            this.setState({
                dropDownIndex: -1,
                userTyping: false,
                littleArrowClicked: false,
            });

            if (dropDownIndex > -1) {
                e.stopPropagation();
            }
            break;
        }
        case 'Escape': {
            if (dropDownIndex !== -1) {
                this.setState({
                    dropDownIndex: -1,
                    userTyping: false,
                    littleArrowClicked: false,
                });
                e.stopPropagation();
            } else {
                onCancel(e);
            }
            break;
        }
        case 'ArrowUp':
        case 'ArrowDown': {
            const diff = e.key === 'ArrowUp' ? -1 : 1;
            this.setState({
                dropDownIndex: (dropDownIndex + diff + options.length) % options.length,
            });
            e.preventDefault();
            break;
        }
        default:
            break;
        }
    }

    onMouseDown(e) {
        e.preventDefault();
        this.inputRef.focus();
        this.setState({
            littleArrowClicked: !this.shouldRenderOptions(),
            dropDownIndex: -1,
            userTyping: false,
        });
    }

    optionClicked(value) {
        const { onSetValue } = this.props;
        onSetValue(value);
        this.setState({
            dropDownIndex: -1,
            userTyping: false,
            littleArrowClicked: false,
        });
    }

    shouldRenderOptions() {
        const { options, LinkComponent, editable } = this.props;
        const { dropDownIndex, littleArrowClicked, userTyping } = this.state;
        return !!(editable &&
                  (LinkComponent || options.length) &&
                  (userTyping || dropDownIndex !== -1 || littleArrowClicked)
        );
    }

    render() {
        const {
            autoFocus,
            editable,
            isTruncated,
            LinkComponent,
            name,
            options,
            placeholder,
            value,
            styleName,
        } = this.props;
        const {
            dropDownIndex,
        } = this.state;

        const style = getStyle(styleName);

        const shouldRenderOptions = this.shouldRenderOptions();

        const optionElements = options.slice(0, 16).map(({ value: val, text }, i) => (
            <li
                key={val}
                onMouseDown={e => e.preventDefault()}
                onClick={() => this.optionClicked(val)}
                style={i === dropDownIndex ? style.optionHighlighted : style.option}
                data-handle={`option-${val}`}
            >
                {text || val}
                &nbsp;
            </li>
        ));
        if (isTruncated) {
            optionElements.push(
                <li
                    data-handle="is-truncated"
                    key="is-truncated"
                    style={style.option}
                    title="The list of values is truncated"
                >
                    ...
                </li>,
            );
        }
        if (LinkComponent) {
            optionElements.push(
                <li
                    key="dependency-link"
                    data-handle="dependency-link"
                    onMouseDown={e => e.preventDefault()}
                    style={style.depOption}
                >
                    <LinkComponent />
                </li>,
            );
        }

        return (
            <div style={style.relative}>
                <div
                    style={shouldRenderOptions ? style.focus : style.wrapper}
                    data-handle={`combobox-${name}`}
                >
                    <input
                        autoFocus={autoFocus}
                        onBlur={this.onBlur}
                        onChange={this.onChange}
                        onFocus={this.onFocus}
                        onKeyDown={this.onKeyDown}
                        onMouseDown={this.onMouseDown}
                        placeholder={placeholder}
                        style={editable ? style.inputField : style.inputFieldReadOnly}
                        readOnly={!editable}
                        type="text"
                        value={value}
                        ref={(r) => { this.inputRef = r; }}
                    />
                    { optionElements.length > 0 && (
                        <button
                            type="button"
                            onMouseDown={this.onMouseDown}
                            style={style.arrow}
                            tabIndex="-1"
                        >
                            &#9662;
                        </button>
                    )}
                </div>
                <div>
                    { shouldRenderOptions && (
                        <ul style={style.listbox}>
                            { optionElements }
                        </ul>
                    )}
                </div>
            </div>
        );
    }
}

InputComboBox.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    isTruncated: PropTypes.bool,
    LinkComponent: PropTypes.oneOfType([
        PropTypes.elementType,
        PropTypes.bool,
    ]),
    name: PropTypes.string.isRequired,
    onBlur: PropTypes.func,
    onChange: PropTypes.func,
    onCancel: PropTypes.func,
    onFocus: PropTypes.func,
    onSetValue: PropTypes.func,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        text: PropTypes.string,
    })),
    placeholder: PropTypes.string,
    styleName: PropTypes.string,
    value: PropTypes.string,
};

InputComboBox.defaultProps = {
    autoFocus: false,
    isTruncated: false,
    options: [],
    info: [],
    value: '',
    onSetValue: () => (null),
    onChange: () => (null),
    onBlur: () => (null),
    onFocus: () => (null),
};

export default InputComboBox;
