import PropTypes from 'prop-types';
import React from 'react';
import animate from '@webui-one/core/utils/animate';
import {
    isNonEmptyArray,
} from '@webui-one/core/utils/array';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Loader from '../../Loading/Loader';
import styles from './ListStyles';
import ListArrow from './static/ListArrow';

const getColumnsStyle = (numCols, numStatic, numRows) => {
    let columns = [];
    if (numRows > 0) {
        columns = [
            ...Array(numStatic).fill('auto'),
            ...Array(numCols - numStatic).fill('minmax(min-content, 100%)'),
        ];
    }

    return {
        display: 'grid',
        gridTemplateColumns: columns.join(' '),
    };
};

const getNumberOfPads = (offset, numRows, pageLength) => {
    if (offset > 1 && (numRows % pageLength) !== 0) {
        return pageLength - (numRows % pageLength);
    }
    return 0;
};
class List extends React.Component {
    constructor(props) {
        super(props);
        this.navRight = this.navRight.bind(this);
        this.navLeft = this.navLeft.bind(this);
        this.onScroll = this.onScroll.bind(this);
        this.onResize = this.onResize.bind(this);
        this.references = [];
        this.scrollingColumnsW = [];
        this.maxScrolled = 0;
        this.scrollLeft = 0;
        this.getColumnStyle = this.getColumnStyleGeneral;
        this.state = {
            canNav: false,
            visibleColumns: {},
        };
    }
    componentDidMount() {
        this.checkColumnWidths();
        this.setScrollNavigationAvailability();
        window.addEventListener('resize', this.onResize);
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { columns: oldColumns } = this.props;
        const { columns, rows } = nextProps;
        if (columns.length !== oldColumns.length || !rows.length) {
            this.resetColumns();
        }
    }

    componentDidUpdate() {
        this.checkColumnWidths();
        this.setScrollNavigationAvailability();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.onResize);
    }

    onScroll() {
        this.setScrollNavigationAvailability();
        this.forceUpdate();
    }

    onResize() {
        this.resetColumns();
    }

    getColumnStyleGeneral(rowIndex, colIndex, column) {
        const { styleName, columns, isOperdata } = this.props;
        let cellStyle;
        const style = styles(styleName).list.row;
        const isEvenRow = ((rowIndex % 2) === 0);

        if (isEvenRow) {
            cellStyle = isOperdata ? style.evenOper : style.even;
        } else {
            cellStyle = style.odd;
        }

        if (column.isSticky) {
            const scrollOffset = (this.scrollContainer || this).scrollLeft;
            return {
                ...cellStyle,
                transform: `translateX(${scrollOffset}px)`,
            };
        }
        if (column.isExpandable) {
            const nrVisibleColumns = columns.filter(c => !c.isExpandable).length;
            return {
                ...cellStyle,
                backgroundColor: defaultStyles.color3,
                borderTop: `4px solid ${defaultStyles.color20}`,
                borderBottom: `1px solid ${defaultStyles.color20}`,
                gridColumn: `1 / span ${nrVisibleColumns}`,
            };
        }
        return cellStyle;
    }

    getColumnHeaderStyle(index) {
        const { shouldRetainColumnsSize, styleName } = this.props;
        const headerStyle = styles(styleName).list.additionalHeaderWrapper;

        // Retain column widths for pagination
        const minWidth = shouldRetainColumnsSize && this.scrollingColumnsW[index] ?
            `${this.scrollingColumnsW[index]}px` : 'auto';

        return { ...this.getColumnStyle(1, index + 1, {}), ...headerStyle, minWidth };
    }

    setScrollNavigationAvailability() {
        const { getScrollWidth, getElementWidth, shouldRetainColumnsSize } = this.props;
        const { canNav, canNavLeft, canNavRight } = this.state;
        const maxScroll = Math.floor(
            getScrollWidth(this.scrollContainer) - getElementWidth(this.scrollContainer));
        this.maxScrolled = Math.max(this.maxScrolled, maxScroll);
        if (!shouldRetainColumnsSize || this.maxScrolled <= 0) {
            return;
        }
        if (!canNav) this.setState({ canNav: true });
        const newCanNavLeft = (this.scrollContainer.scrollLeft > 0);
        const newCanNavRight = (this.scrollContainer.scrollLeft < this.maxScrolled);
        if (newCanNavLeft !== canNavLeft) this.setState({ canNavLeft: newCanNavLeft });
        if (newCanNavRight !== canNavRight) this.setState({ canNavRight: newCanNavRight });
    }

    resetColumns() {
        this.setState({
            canNav: false,
            canNavLeft: false,
            canNavRight: false,
        });
        this.references = [];
        this.scrollingColumnsW = [];
        this.maxScrolled = 0;
    }

    navRight() {
        const { getElementWidth, getScrollWidth, nrOfStaticColumns } = this.props;
        const c = this.scrollContainer;
        let index = nrOfStaticColumns;
        let step = 0;

        while (Math.floor(step) <= c.scrollLeft) {
            step += this.scrollingColumnsW[index];
            index += 1;
        }

        const maxScroll = getScrollWidth(c) - getElementWidth(c);
        const start = c.scrollLeft;
        const end = Math.min(step, maxScroll);

        animate(c, start, end, 200);
        this.scrollLeft = end;
        this.setScrollNavigationAvailability();
    }

    navLeft() {
        const { nrOfStaticColumns } = this.props;
        const c = this.scrollContainer;
        let index = nrOfStaticColumns;
        let step = 0;
        let previousStep = 0;

        while (c.scrollLeft > step) {
            previousStep = step;
            step += this.scrollingColumnsW[index];
            index += 1;
        }

        const start = c.scrollLeft;
        const end = previousStep;

        animate(c, start, end, 200);
        this.scrollLeft = end;
        this.setScrollNavigationAvailability();
    }

    checkColumnWidths() {
        const { rows } = this.props;
        if (rows.length === 0) return;
        const refs = this.references.filter(r => r !== null);
        if (refs.length) {
            const { getElementWidth } = this.props;

            this.scrollingColumnsW = refs
                .map((ref, i) => (
                    Math.max(
                        getElementWidth(ref),
                        this.scrollingColumnsW[i] || 0,
                    )
                ));
            this.updateColumnWidths = false;
        }
    }

    renderRow(row, rowIndex) {
        const { columns } = this.props;
        return columns.filter(
            column => !column.isExpandable ||
                (row.data.expandable && row.data.expandable.isExpanded),
        ).map((column, colIndex) => (
            <div
                style={this.getColumnStyle(rowIndex + 2, colIndex + 1, column)}
                key={colIndex}
                data-handle={`cell-${column.key}-${colIndex}-${rowIndex}-${row.rowKey}`}
            >
                {column.createCell(row.data[column.key], column.key, row.rowKey, row)}
            </div>
        ));
    }

    render() {
        const {
            additionalElements,
            chunkSize,
            columns,
            headerElements,
            isLoading,
            isOperdata,
            name,
            nrOfStaticColumns,
            offset,
            rows,
            shouldPadRows,
            styleName,
        } = this.props;
        const {
            canNav,
            canNavLeft,
            canNavRight,
        } = this.state;
        const style = styles(styleName).list;
        let rowElements = [];
        let columnHeaders = [];
        const numPads = getNumberOfPads(offset, rows.length, chunkSize);

        if (rows.length > 0) {
            columnHeaders = columns.filter(c => !c.isExpandable).map((c, index) => (
                <div
                    style={this.getColumnHeaderStyle(index)}
                    key={`header-${c.key}`}
                    data-handle={`header-${c.key}`}
                    ref={(r) => {
                        this.references[index] = r;
                    }}
                >
                    {c.createHeader(c.key, c.title)}
                </div>
            ));
            rowElements = rows.map((row, rowIndex) => this.renderRow(row, rowIndex));
        }

        let paddingBottom = 0;
        if (shouldPadRows && numPads > 0) {
            const singleHeight = parseFloat(style.cellWrapper.height);
            const height = `${singleHeight * numPads}rem`;
            paddingBottom = height;
        }

        return (
            <div data-handle={name} style={isOperdata ? style.mainOper : style.main}>
                <div style={style.nameContainer}>
                    {headerElements}
                    {!isNonEmptyArray(rowElements) && additionalElements}
                </div>
                <div style={style.background}>
                    {canNav && (
                        <button
                            type="button"
                            style={canNavLeft ? style.navButton : style.navButtonDisabled}
                            disabled={!canNavLeft}
                            onClick={this.navLeft}
                            data-handle="navLeft"
                        >
                            <ListArrow color={defaultStyles.color1} />
                        </button>
                    )}
                    <div style={style.wrapper}>
                        <div style={isNonEmptyArray(rowElements)
                                    ? style.relative : style.relativeEmpty}
                        >
                            <div
                                ref={(r) => { this.scrollContainer = r; }}
                                style={{
                                    ...style.body,
                                    ...getColumnsStyle(
                                        columns.filter(c => !c.isExpandable).length,
                                        nrOfStaticColumns,
                                        rows.length,
                                    ),
                                    paddingBottom,
                                }}
                                data-handle="scrollContainer"
                                onScroll={this.onScroll}
                            >
                                {[...columnHeaders, ...rowElements]}
                            </div>
                            {canNavRight && <div style={style.rightShadow} />}
                        </div>
                    </div>
                    {canNav && (
                        <button
                            type="button"
                            style={canNavRight ? style.navButton : style.navButtonDisabled}
                            onClick={this.navRight}
                            disabled={!canNavRight}
                            data-handle="navRight"
                        >
                            <ListArrow color={defaultStyles.color1} direction="right" />
                        </button>
                    )}
                </div>
                {isNonEmptyArray(rowElements) && (
                    <div style={canNav ? style.footerNav : style.footer} >
                        {additionalElements}
                    </div>
                )}
                {isLoading && (
                    <div style={style.loading}>
                        <Loader color={defaultStyles.color9} />
                    </div>
                )}
            </div>
        );
    }
}


List.propTypes = {
    chunkSize: PropTypes.number,
    additionalElements: PropTypes.arrayOf(Object),
    columns: PropTypes.arrayOf(Object).isRequired,
    getElementWidth: PropTypes.func,
    getScrollWidth: PropTypes.func,
    isLoading: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.shape({}),
    ]),
    isOperdata: PropTypes.bool,
    headerElements: PropTypes.arrayOf(Object),
    name: PropTypes.string,
    nrOfStaticColumns: PropTypes.number,
    offset: PropTypes.number,
    rows: PropTypes.arrayOf(Object),
    shouldPadRows: PropTypes.bool,
    shouldRetainColumnsSize: PropTypes.bool,
    styleName: PropTypes.string,
};

List.defaultProps = {
    additionalElements: [],
    chunkSize: 10,
    getElementWidth: element => element.getBoundingClientRect().width,
    getScrollWidth: element => element.scrollWidth,
    headerElements: [],
    isLoading: false,
    nrOfStaticColumns: 0,
    rows: [],
    shouldPadRows: true,
    shouldRetainColumnsSize: true,
};

export default List;

export {
    getNumberOfPads as _getNumberOfPads,
};
