import * as S from '../schema';
import { quoteKey } from './key';

export const sortRows = (rows, sortOrder) => {
    if (!sortOrder || rows.length < 2) return rows;

    const { columnKey, isDesc } = sortOrder;
    const ord = isDesc ? -1 : 1;
    return [...rows].sort((ad, bd) => {
        let a = ad.data[columnKey];
        let b = bd.data[columnKey];
        if (typeof a === 'string' && typeof b === 'string') {
            return a.localeCompare(b) * ord;
        }

        if (typeof a !== typeof b) {
            a = parseInt(a, 10) || -Infinity;
            b = parseInt(b, 10) || -Infinity;
        }

        if (a > b) return 1 * ord;
        if (a < b) return -1 * ord;
        return 0;
    });
};

export const getColumnChildren = (schema) => {
    const hasChildren = schema && schema.data && schema.data.children;
    let keyNames = hasChildren && schema.data.children
        .filter(c => ['key', 'leaf'].indexOf(S.kind(c)) > -1)
        .map(c => ({
            name: S.name(c),
            kind: S.kind(c),
            isLeafList: S.isLeafList(c),
            isEmptyLeaf: S.isEmptyLeaf(c),
        }));
    const keys = schema && schema.data && S.keyNames(schema);
    if (keys && (!keyNames || keyNames.length === 0)) {
        keyNames = keys.map(name => ({
            name,
            kind: 'key',
            isLeafList: false,
            isEmptyLeaf: false,
        }));
    }
    return keyNames;
};

export const getDataFromValueItems = items =>
    Object.entries(items).reduce((data, [itemName, itemValue]) => {
        const isLeaf = itemValue.kind === 'leaf' && itemValue.value;
        if (isLeaf) {
            return {
                ...data,
                [itemName]: itemValue.value,
            };
        }
        return data;
    }, null);

export const getKeyValue = (items, skipQuotesOnKeys) => {
    const values = Object.keys(items).reduce((acc, cur) => {
        const curObj = items[cur];
        if ((curObj.kind === 'key' || curObj.kind === 'leaf-list') && curObj.value !== undefined) {
            acc.push(skipQuotesOnKeys ? curObj.value : quoteKey(curObj.value));
        }
        return acc;
    }, []);
    return values.join(' ');
};

export const getQuerySelection = ({ isLeafList, columnChildren }) => {
    if (isLeafList === true) {
        return undefined;
    }
    return columnChildren.map(c => c.name);
};

export const isVisibleColumn = (columnsVisibility, c) =>
    !columnsVisibility ||
    !(c.key in columnsVisibility) ||
        columnsVisibility[c.key].includes('VISIBLE');

export const unzipRow = (selection, rows) => {
    if (selection.length > 0 && rows.length > 0) {
        return rows.map(row => (
            selection.map(prop => (row[prop]))
        ));
    }
    return [];
};
