import React from 'react';
import PropTypes from 'prop-types';

import InputComboBox from './InputComboBox';

const filterIncludes = (options, value) =>
    options.filter(o => o.value.toLowerCase().includes(value.toLowerCase()));

class ComboBox extends React.Component {
    constructor(props) {
        super(props);
        this.onBlur = this.onBlur.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onSetValue = this.onSetValue.bind(this);
        this.state = {
            draftValue: props.value,
            filterValue: '',
            hasFocus: false,
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { hasFocus } = state;
        const { isLoading, value } = props;
        if (!hasFocus && !isLoading) {
            return { draftValue: value };
        }
        return null;
    }

    // e.g user has changed the value
    handleChange(e) {
        const { onChange } = this.props;
        this.setState({
            draftValue: e.target.value,
            filterValue: e.target.value,
        });
        if (onChange) onChange(e.target.value);
    }

    onSetValue(newValue) {
        const { onSetValue, value } = this.props;

        if (value !== newValue || newValue === '') {
            onSetValue(newValue);
            this.setState({
                draftValue: newValue,
                filterValue: '',
            });
        }
    }

    onCancel(e) {
        const { value } = this.props;
        this.setState({
            draftValue: value,
        },
        () => {
            e.target.blur();
        });
    }

    onFocus() {
        this.setState({
            hasFocus: true,
            filterValue: '',
        });
    }

    onBlur() {
        this.setState({
            hasFocus: false,
        });
    }

    render() {
        const { filterOptions, options, isDefaultValue, maxLimit } = this.props;
        const { filterValue, draftValue, hasFocus } = this.state;
        const draftValueDefault = (isDefaultValue && !hasFocus) ? `(${draftValue})` : draftValue;
        const isTruncated = maxLimit !== undefined && maxLimit < options.length;
        return (
            <div>
                <InputComboBox
                    {...this.props}
                    isTruncated={isTruncated}
                    value={draftValueDefault}
                    onBlur={this.onBlur}
                    onCancel={this.onCancel}
                    onChange={this.handleChange}
                    onFocus={this.onFocus}
                    onSetValue={this.onSetValue}
                    options={filterOptions(options, filterValue).slice(0, maxLimit)}
                />
            </div>
        );
    }
}

ComboBox.propTypes = {
    isLoading: PropTypes.bool,
    isDefaultValue: PropTypes.bool,
    maxLimit: PropTypes.number,
    onChange: PropTypes.func,
    onSetValue: PropTypes.func,
    filterOptions: PropTypes.func,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        text: PropTypes.string,
    })),
    value: PropTypes.string,
};

ComboBox.defaultProps = {
    options: [{
        value: '',
        text: 'empty',
    }],
    value: '',
    filterOptions: filterIncludes,
};

export default ComboBox;

export {
    ComboBox as _ComboBox,
    filterIncludes as _filterIncludes,
};
