import React from 'react';
import PropTypes from 'prop-types';
import {
    timeStampToISOLocale,
} from '@webui-one/core/utils/time';
import ToolTip from '../../ToolTip/ToolTip';
import styles from './RelativeTimeStampStyles';

export default class RelativeTimeStamp extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.timeUpdated = this.timeUpdated.bind(this);
    }

    componentDidMount() {
        const { timer } = this.props;
        timer.subscribe(this.timeUpdated);
    }

    componentWillUnmount() {
        const { timer } = this.props;
        timer.unsubscribe(this.timeUpdated);
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    timeUpdated() {
        this.forceUpdate();
    }

    render() {
        const { timeStamp, timeFormatter } = this.props;
        const toolTipStr = timeStamp ? timeStampToISOLocale(timeStamp) : 'N/A';
        const str = timeStamp ? timeFormatter(timeStamp) : 'N/A';

        return (
            <div
                role="tooltip"
                style={styles.cellWrapper}
                onMouseDown={this.showTooltip}
                onMouseUp={this.hideTooltip}
                onMouseEnter={this.showTooltip}
                onMouseLeave={this.hideTooltip}
            >
                <div data-handle="cell-date" style={styles.cell}><time dateTime={toolTipStr}>{str}</time></div>
                <ToolTip direction="right" toolTipText={toolTipStr} ref={(tt) => { this.tooltip = tt; }} />
            </div>
        );
    }
}

RelativeTimeStamp.propTypes = {
    timeFormatter: PropTypes.func.isRequired,
    timeStamp: PropTypes.number,
    timer: PropTypes.shape(Object),
};
